<template>
  <div class="container">
    <Sidebar />
    <main>
      <h1>UPDATE RESPONSE</h1>
      <section>
        <form @submit.prevent="save">
          <div class="form-group">
            <label for="route">ENDPOINT</label>
            <input
              type="text"
              class="inp"
              id="route"
              readonly
              :value="$route.params.route"
            />
          </div>
          <div class="form-group">
            <label for="userid">USER-ID</label>
            <input
              type="text"
              class="inp"
              id="userid"
              readonly
              :value="token"
            />
          </div>
          <div class="form-group">
            <label class="res" for="response">RESPONSE</label>
            <textarea required placeholder="Paste JSON here" class="xl" v-model="data">
            </textarea>
          </div>
          <div class="form-group">
            <label></label>
            <button type="submit">SAVE</button>
          </div>
        </form>
      </section>
    </main>
  </div>
</template>

<script>
import Sidebar from "../components/Sidebar.vue";
export default {
  name: "Update",
  components: {
    Sidebar,
  },
  data() {
    return {
      data: "",
    };
  },
  computed: {
    token() {
      return this.$store.getters.token;
    },
  },
  methods: {
    save() {
      const res = {
        id: this.token,
        route: this.$route.params.route,
        response: JSON.parse(this.data),
      };
      fetch(`${process.env.VUE_APP_URI}routes/update`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(res),
      })
        .then((response) => response.json())
        .then((response) => {
          this.$router.push({ name: "Dashboard" });
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  display: flex;
}
main {
  width: 100%;
  h1 {
    padding: 30px 0 0 30px;
    text-align: left;
    font-weight: 600;
    height: 10%;
  }
}
section {
  height: 90%;
  padding: 30px;
}
form {
  padding: 30px;
}
.form-group {
  display: flex;
  align-items: center;
  width: 60%;
  padding: 30px;
  label {
    flex: 1;
    text-align: left;
    font-size: 1.5rem;
  }
  input,
  .inp {
    flex: 2;
    background: #202020;
    border-radius: 10px;
    height: 50px;
    text-align: center;
    border: none;
    outline: none;
    color: #59c3ff;
    font-family: inherit;
    letter-spacing: 2px;
    font-size: 1.3rem;
    width: 80%;
  }
  .xl {
    flex: 2;
    height: 300px;
    background: #202020;
    border-radius: 10px;
    font-size: 1rem;
    font-family: inherit;
    width: 80%;
    text-align: left;
    padding: 10px;
    resize: none;
    outline: none;
    border: none;
    color: #59c3ff;
    white-space: pre;
  }
  .res {
    align-self: start;
  }
  button {
    color: #06ff88;
    cursor: pointer;
    background: #202020;
    border-radius: 10px;
    text-align: center;
    border: none;
    outline: none;
    height: 50px;
    font-size: 1.25rem;
    flex: 1;
  }
}
</style>